@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}

@-moz-keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}

@keyframes Gradient {
  0% {
    background-position: 0% 50%
  }
  50% {
    background-position: 100% 50%
  }
  100% {
    background-position: 0% 50%
  }
}

body {
  /*background-color: black;*/
    /*background: linear-gradient(-45deg, #EE7752, #E73C7E, #23A6D5, #23D5AB);*/
    /*background: linear-gradient(-45deg, #8b2b0e, #88113f, #156684, #15846a);*/
    background: linear-gradient(-45deg, #461607, #44081f, #0b3342, #0b4235);
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    -moz-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
}

.container {

    min-height: 101vh;
    display: flex;
    align-items: center;
}

.row {
    width: 100%;
}

@font-face {
  font-family: 'alef-bold';
  font-style: normal;
  src: url('/Alef-Bold.ttf');
}

@font-face {
  font-family: 'alef-regular';
  font-style: normal;
  src: url('/Alef-Regular.ttf');
}

h1 {
  font-family: 'alef-regular';
  color: white;
  margin-top: -10px;
  margin-bottom: 0px;
  font-size: 55px;
  /*text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 10px;*/
}

h2 {
  font-family: 'alef-bold';
  color: white;
  margin-bottom: 0px;
  font-size: 45px;
  /*text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 10px;*/
}

h3 {
  font-family: 'alef-regular';
  color: white;
  margin-bottom: 0px;
  font-size: 25px;
  /*text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 10px;*/
}

a {
  display: block;
  text-align: right;
  color: rgba(255, 254, 255, 0.5);
  transition: color 0.2s;
  font-family: 'alef-bold';
  font-size: 25px;
  text-shadow: rgba(0, 0, 0, 0.5) 0px 0px 10px;
}

a:hover {
  color: white;
  text-decoration: none;
  color: white;
  text-shadow: none;
}

a.active {
  color: white;
  text-shadow: none;
  animation-name: activeIntro;
  /*animation-delay: 0.2s;*/
  animation-duration: 0.3s;
}

@keyframes activeIntro {
  0% {margin-right: 0px;}
  25% {margin-right: 3px;}
  50% {margin-right: 0px;}
  75% {margin-right: 2px;}
  100% {margin-right: 0px;}
}

.nav-links {
  padding-top: 15px;
  /*padding: 15px;*/
}

.nav-links > a {
  transition: color 0.5s;
}

body {
  /*opacity: 0;*/
