.page {
  /*min-height: 400px;*/
  display: flex;
  opacity: 0;
  transition: opacity 200ms, transform 200ms, left 200ms;
  /*transition-delay: transform 50ms;*/
  position: relative;

  position: absolute;
  transform: translateX(-25px);
}

.page.from-left {
  transform: translateX(25px);
}


.page a:not(.project-tile) {
  text-align: left;
  font-size: 20px;
  transition: none;
}

.page a:not(.project-tile):hover {
  color: white;
  text-decoration: underline;
  text-shadow: none;
}

.back-button-link {
  height: 30px;
  position: absolute;
    right: 0px;
    top: -10px;
}

.back-arrow {
  width: 41px;
  height: 30px;
  margin-left: 15px;
}

